export const userErrors = {
  423: '   ',
  400: '  ',
  204: '   ',
};

export const validateName = (text) => {
  if (!text) {
    return ' ';
  } else if (/^[A-Za-z0-9_-]+$/.test(text)) {
    return 0;
  } else
    return '      , , _  -';
};

export const validatePassword = (text) => {
  if (!text) {
    return ' ';
  } else if (!/^[A-Za-z0-9_]+$/.test(text)) {
    return '      ,   _';
  } else if (!/.{5,}/.test(text)) {
    return '   5   ';
  } else if (!/[a-zA-Z]+/.test(text) || !/[0-9]+/.test(text)) {
    return '       ';
  } else return 0;
};

export const validateInputQuestion = (text) => {
  if (!text) {
    return '    ';
  }
  return 0;
};
